@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

input[type="submit"], button { cursor: pointer; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }
.slick-list:focus { outline: none; }
.slick-loading .slick-list { background: white; }
.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-list, .slick-track, .slick-slide, .slick-slide img { transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; zoom: 1; }
.slick-track:before, .slick-track:after { content: ""; display: table; }
.slick-track:after { clear: both; }
.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; }
.slick-slide img { display: block; max-width: 100%; }
.slick-slide.slick-loading img { display: none; }
.slick-slide.dragging img { pointer-events: none; }
.slick-initialized .slick-slide { display: block; }
.slick-loading .slick-slide { visibility: hidden; }
.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-prev, .slick-next { position: absolute; top: 50%; margin-top: -10px; font: 0/0 a; text-shadow: none; color: transparent; height: 20px; background: none; left: 0; z-index: 1; }
.slick-prev:focus, .slick-next:focus { outline: none; }
.slick-prev.slick-disabled, .slick-next.slick-disabled { opacity: 0.2; }
.slick-prev:before, .slick-next:before { height: 30px; line-height: 30px; content: "prev"; display: inline-block; vertical-align: middle; font-size: 15px; color: #fff; background: #000; }

.slick-next { right: 0; left: auto; }
.slick-next:before { content: "next"; display: inline-block; }

.slick-dots { text-align: center; position: absolute; margin: 0; bottom: 0; left: 0; right: 0; }
.slick-dots li { position: relative; display: inline-block; padding: 0 3px; }
.slick-dots li.slick-active button { background: #000; }
.slick-dots button { display: block; width: 10px; height: 10px; background: #fff; box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25); cursor: pointer; font: 0/0 a; text-shadow: none; color: transparent; border-radius: 50%; }

body { font-family: Arial, sans-serif; font-size: 12px; line-height: 1.4; }

.out { width: 1100px; margin: 0 auto; }

.header { border-bottom: 1px solid #ccc; position: relative; z-index: 20; }
.header__lang { position: absolute; top: 16px; right: 0; text-align: right; }
.header__lang a { color: #ccc; text-decoration: none; margin-left: 10px; }
.header__lang a.is-active, .header__lang a:hover { color: #000; }
.header__search { position: absolute; top: 10px; right: 100px; }
.header__search input { border-bottom: 1px solid #000; height: 23px; }
.header__search button { background: none; font-size: 15px; position: absolute; right: 0; top: 0; }
.header__user { position: absolute; top: 50px; right: 0; font-size: 16px; }
.header__user a { color: #000; margin-left: 15px; text-decoration: none; }
.header__user i { font-size: 20px; margin-right: 5px; }
.header__logo { font-family: Georgia, serif; font-size: 70px; font-weight: normal; }
.header .nav { border-top: 1px solid #ccc; }
.header .nav li { display: inline-block; position: relative; }
.header .nav li:hover > a { text-decoration: underline; background: #000; color: #fff; }
.header .nav li:hover .nav__dropdown { display: block; }
.header .nav li a { display: inline-block; padding: 0px 20px; height: 50px; line-height: 50px; font-size: 16px; text-transform: uppercase; font-family: Georgia, serif; text-decoration: none; color: #000; margin-right: 15px; }
.header .nav li a:hover { text-decoration: underline; background: #000; color: #fff; }
.header .nav li .nav__dropdown { display: none; position: absolute; top: 51px; left: 0; width: 200px; background: #fafafa; box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3); }
.header .nav li .nav__dropdown a { margin: 0; display: block; }
.header .nav li .nav__dropdown dd { font-size: 13px; }
.header .nav li .nav__dropdown.is-special { width: 600px; padding: 0 0 15px 0; }
.header .nav li .nav__dropdown.is-special dl { float: left; width: 50%; }
.header .nav li .nav__dropdown.is-special a { display: block; margin-right: 0; height: auto; line-height: 1; text-transform: none; }
.header .nav li .nav__dropdown.is-special a:hover { background: none; color: #000; }
.header .nav li .nav__dropdown.is-special dt { font-weight: normal; margin-bottom: 6px; padding: 10px 0; }
.header .nav li .nav__dropdown.is-special dt a { font-size: 21px; color: #999; }
.header .nav li .nav__dropdown.is-special dd { margin-bottom: 7px; }

.banner { position: relative; margin-bottom: 40px; }
.banner__next, .banner__prev { position: absolute; top: 50%; left: 10px; color: #fff; font-size: 50px; transform: translate(0, -50%); cursor: pointer; }
.banner__next { right: 10px; left: auto; }
.banner .banner__slide { color: #fff; height: 50vh; text-align: center; box-sizing: border-box; padding-top: 60px; background-size: cover; padding-left: 100px; padding-right: 100px; }
.banner p { font-size: 18px; }
.banner h1 { font-size: 40px; margin-bottom: 15px; }

.list { margin-bottom: 60px; overflow: hidden; }
.list h2 { font-size: 33px; font-family: Georgia, serif; position: relative; margin-bottom: 20px; }
.list h2 a { font-size: 14px; position: absolute; right: 0; bottom: 0; color: #f60; }

.item { float: left; width: 25%; text-decoration: none; position: relative; color: #000; }
.item:hover .item__title { text-decoration: underline; }
.item:hover .item__pic { transform: translate(0, -10px); }
.item__pic { transition: all 0.2s linear; }
.item__title { font-size: 18px; font-weight: normal; font-family: Georgia, serif; display: inline-block; }
.item__price { font-family: Georgia, serif; position: absolute; left: 50%; top: 0; transform: rotate(-35deg); background: #f60; color: #fff; padding: 5px; display: inline-block; }

.theme { float: left; width: 20%; text-align: center; text-decoration: none; color: #000; padding: 15px 0 0 0; }
.theme .theme__pic { transition: all 0.2s linear; }
.theme:hover .theme__pic { transform: translate(0, -10px); }
.theme__title { font-size: 18px; font-weight: normal; font-family: Georgia, serif; display: inline-block; }

.promo { margin: 50px 0; text-align: center; border: 5px dashed #ccc; padding: 40px 0; }
.promo a { display: inline-block; width: 35%; border-radius: 10px; padding: 15px 36px; font-size: 30px; font-family: Georgia, serif; background-color: #f60; background-image: linear-gradient(#f60, #cc5200); color: #fff; text-decoration: none; margin: 0 35px; }
.promo a:hover { background-color: #cc5200; background-image: linear-gradient(#cc5200, #f60); }

.footer { border-top: 1px solid #ccc; padding: 15px; overflow: hidden; }
.footer .footer__col { float: left; width: 30%; }
.footer .footer__col h3 { font-size: 21px; color: #777; font-weight: normal; margin-bottom: 14px; }
.footer .footer__col li { margin-bottom: 10px; }
.footer .footer__col a { color: #000; }
.footer .footer__copy { clear: right; float: right; padding-top: 10px; color: #777; }
.footer .footer__socials { font-size: 30px; color: #777; text-align: right; float: right; }
.footer .footer__socials a { color: #777; margin-left: 10px; }
.footer .footer__socials a:hover { color: #000; }


